import { Link } from "gatsby";
import React from "react";
import * as styles from "../styles/nav.module.css";
import logo from "../images/Chefs Choice Logo.png";


export default function Nav(props) {

// Window Undefined Handle
if(typeof window === "undefined") {
  return;
}

  // Language 
  const [language, setLanguage] = React.useState('');
  React.useEffect(() => {
  setLanguage(window.localStorage.getItem('CC-Language'));
  }, []);


  return (
    <div className={styles.Nav}>

        <div className={styles.fade} id="fade"></div>

        <div className={styles.navWindow} id="nav">
          <img src={logo} alt="logo" className={styles.logo}/>
          <Link to="/" className={styles.item} onClick={props.handleNavToggle}>{language === "tr-TR" ? "ANASAYFA" : "HOME"}</Link>
          <Link to={"/products" + window.location.search.slice(0,3) + "&cat"} className={styles.item} onClick={props.handleNavToggle}>{language === "tr-TR" ? "KEDİLER" : "FOR CATS"}</Link>
          <Link to={"/products" + window.location.search.slice(0,3) + "&dog"} className={styles.item} onClick={props.handleNavToggle}>{language === "tr-TR" ? "KÖPEKLER" : "FOR DOGS"}</Link>
          <Link to="/about" className={styles.item} onClick={props.handleNavToggle}>{language === "tr-TR" ? "HAKKIMIZDA" : "ABOUT"}</Link>
          <Link to="/contact" className={styles.item} onClick={props.handleNavToggle}>{language === "tr-TR" ? "İLETİŞİM" : "CONTACT US"}</Link>
        </div>

    </div>
  )
}   